@keyframes bounce {
  0%,
  100%,
  20%,
  50%,
  80% {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-10px);
    -ms-transform: translateY(-10px);
    transform: translateY(-10px);
  }
  60% {
    -webkit-transform: translateY(-5px);
    -ms-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}

@keyframes title-hover {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-15px);
  }
}

header,
main {
  text-align: center;
}

main {
  padding: 0 30px;
}

header {
  width: 100%;
  height: auto;
  color: white;
  position: relative;
  overflow: hidden;
  perspective: 100px;
  animation: colorChange 25s ease infinite alternate;
  // background-color: #3178d5;
  // background-image: linear-gradient(
  //   to bottom,
  //   #3178d5,
  //   #2983d9,
  //   #278edc,
  //   #2e99df,
  //   #3aa3e0,
  //   #4eabe1,
  //   #71bce6,
  //   #85c4e6,
  //   #99cce6,
  //   #aed1e4,
  //   #ffffff,
  //   #ffffff
  // );
  background-image: radial-gradient(
    ellipse at 50% 36%,
    #142c63 0,
    #141e36 45%,
    #000 150%
  );
}

.branding {
  height: 100vh;
  width: 100%;
  padding: 0 3rem;
  z-index: 15;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: transparent;
}

.pronounce {
  padding-top: 1rem;
  font-size: 20px;
  position: absolute;

  i {
    font-size: 16px;
  }
  &:hover {
    .explanation {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.explanation {
  margin-top: 1rem;
  opacity: 0;
  transition: opacity 300ms ease;
  color: black;
  padding: 0.5rem 1rem 1rem;
  pointer-events: none;
  background-color: white;
  font-style: italic;
  border-radius: 3px;

  span {
    font-size: 12px;
    font-style: normal;
  }
}

h1 {
  font-family: "Aquatico";
  font-size: 4.75rem;
  text-transform: uppercase;
  
  span {
    background-color: white;
    background-image: linear-gradient(0deg, white 40%, #93d8fb 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: all 300ms ease;
    text-shadow: none;

    &:hover {
      padding: 0 10px;
      color: #fff;
      text-align: center;
      text-transform: uppercase;
      font-family: "aquatico", sans-serif;
      font-weight: 200;
      letter-spacing: 2px;
      text-shadow: 0 0 5px #fff, 0 0 10px #60c0f0, 0 0 20px #60c0f0;
      transform: translateY(0px);
    }
  }

  @media screen and (max-width: 400px) {
    font-size: 3rem;
  }
}

@media screen and (min-width: 568px) {
  h1 {
    font-size: 7rem;
  }

  h2 {
    font-size: 1.5rem;
    flex-direction: row;

    span {
      font-size: 0.75rem;
    }
  }
}

figure {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 60px;
  perspective: 100px;
  transition: perspective-origin 400ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  > div {
    transform: translate3d(0px, 0px, 20px) !important;
  }
}

@keyframes animateclouds {
  100% {
    transform: translateX(110vw);
  }
}

.sun {
  width: 200px;
  height: 200px;
  background-color: #fefefe;
  border-radius: 50%;
  position: absolute;
  top: 25%;
  left: 65%;
  transform: translateX(-50%);
  transition: background-color 1s ease;

  &::before {
    content: "";
    display: block;
    position: absolute;
    background: #fefefe;
    border-radius: 50%;
    width: 200%;
    height: 200%;
    left: -50%;
    top: -50%;
    -webkit-filter: blur(4rem);
    filter: blur(4rem);
  }
}

.cloud {
  animation: animateclouds 150s infinite linear;
  background-color: #f4f8fc;
  border-radius: 3rem;
  border-left: 3px solid #e8f0f7;
  // border-bottom: 3px solid #dde9f4;
  box-shadow: 0 0.5rem 1rem 0.5rem rgba(239, 239, 239, 0.1);
  transform: translateX(-30vw);
  width: 12rem;
  height: 4rem;
  top: 70%;
  left: -10%;
  position: absolute;

  &::before {
    width: 100px;
    height: 100px;
    top: -45px;
    left: 25px;
    z-index: 7;
    background: #f0f8ff;
    border-radius: 50%;
    border-left: 3px solid #e8f0f7;
    content: "";
    display: block;
    position: absolute;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    background: #f0f8ff;
    border-radius: 50%;
    border-left: 3px solid #e8f0f7;
    width: 60px;
    z-index: 7;
    height: 60px;
    top: -20px;
    left: 100px;
  }

  &.cloud1 {
    top: 20%;
    animation: animateclouds 75s infinite linear;
    transform: scale(0.7) translateX(-30vw);
    z-index: 8;
  }

  &.cloud2 {
    top: 40%;
    animation: animateclouds 50s infinite linear;
    transform: scale(0.92) translateX(-30vw);
    z-index: 0;
  }
}

.islands {
  height: 50%;
  top: 25%;
  position: absolute;
  left: 25%;
  transform: translate(-50%, -50%);
  width: 50%;
  z-index: 8;

  @media screen and (max-width: 700px) {
    width: 100%;
    z-index: 1;
  }
}

.island {
  width: 110px;
  height: 16px;
  border-radius: 5px;
  background: linear-gradient(180deg, rgb(0, 252, 0), limegreen);
  background: linear-gradient(0deg, #65b306, #acf036);
  position: absolute;
  opacity: 0.95;
  animation: title-hover 2s ease-in-out infinite alternate;

  .mountain {
  }

  .waterfall {
    width: 15px;
    height: 200px;
    background-image: linear-gradient(
      0deg,
      rgba(115, 225, 255, 0),
      #73e1ff,
      #0da6ff
    );
    margin-left: 25px;
    margin-top: 0px;
    border-top-left-radius: 4px;

    &.waterfall2 {
      width: 25px;
      height: 500px;
      margin-left: 80px;
      border-top-left-radius: 0;
      border-top-right-radius: 6px;
      background-image: linear-gradient(
        0deg,
        rgba(115, 225, 255, 0),
        rgba(115, 225, 255, 0.7),
        #0da6ff
      );
    }

    &.waterfall3 {
      width: 5px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 16px;
    left: 50%;
    z-index: -1;
    border-bottom-left-radius: 50% 50%;
    border-bottom-right-radius: 50% 50%;
    height: 30px;
    background: #bb8738;
    background: #dd9c42;
    background: #bd7c26;
    // border-bottom: 30px solid #ca923f;
    // border-left: 25px solid transparent;
    // border-right: 25px solid transparent;
    // border-top: 25px solid transparent;
    // box-shadow: 0 50px 15px -10px rgba(0,0,0,0.2);
    box-shadow: 0 10px 15px -10px rgba(0, 0, 0, 0.2);

    width: 100px;
    transform: translateX(-50%);
  }

  &.island1 {
    bottom: 0;
    right: 0;
    z-index: 7;
  }

  &.island2 {
    position: absolute;
    top: 0;
    left: 0;
    width: 150px;

    &::after {
      height: 60px;
      width: 135px;
    }
  }

  &.island3 {
    top: 20%;
    left: 38%;
  }
}

@keyframes colorChange {
  0% {
    background-color: rgb(13, 156, 223);
  }

  100% {
    background-color: rgb(1, 18, 54);
  }
}


@keyframes spin {
  0% {
    transform: translateX(-50%) translateY(-50%) rotate(360deg);
  }
  100% {
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
    
  }
}

@keyframes startravel {
  // 0%   { box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0.9); }
  // 100% { box-shadow: 0 0 15px 0.15em rgba(255, 255, 255, 0.7), 0 0 2em 1px rgba(96, 192, 240, 0.5); }
}
.space {
  width: 1500px;
  height: 1500px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  top: 38%;
  left: 50%;
  animation: spin 150s linear infinite;
  transform-origin: center center;
  pointer-events: none;

  .stars {
    z-index: 11;
    display: block;
    position: absolute;
    border-radius: 50%;
    background: #fff;
    // box-shadow: 0 0 15px 0.15em rgba(255, 255, 255, 0.7), 0 0 2em 1px rgba(96, 192, 240, 0.5);
    //   box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0.9);
    animation: startravel 35s linear infinite;
  }

  @media screen and (min-width: 1600px) {
    width: 2000px;
    height: 2000px;
  }
}

@for $i from 1 through 200 {
  $randomNum: random(8) + px;
  .stars:nth-child(#{$i}) {
    top: percentage(random(100) / 100);
    left: percentage(random(100) / 100);
    width: $randomNum;
    height: $randomNum;
    box-shadow: 0 0 15px 0 rgb(random(255), random(255), random(255)), 0 0 15px 0 rgb(random(255), random(255), random(255));
  }
}
